.ImageGalleryItem {
  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  width: 250px;
  height: 460px;
  padding-bottom: 20px;
}
.CardTitleHomePage {
  padding-left: 5px;
  padding-right: 5px;
}

.ImageGalleryItem-image {
  width: 100%;
  height: 85%;
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.ImageGalleryItem:hover {
  transform: scale(1.03);
  cursor: zoom-in;
}

.ImageGallery {
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}
.LinkOfListHomepage {
  text-decoration: none;
  text-align: center;
}
